import MainForm from './MainForm';
import logo from "../assets/images/logo_white.png"

function FormContainer() {
  return (
    <div className="formContainer">
        <p className="formTitle">سامانه آزمایش پایانه فروش</p>
        <MainForm/>
        <div className='formFooter d-flex justify-content-center align-content-center mt-4'>
            <img className="p-2" src={logo} alt="logo" height={"40px"}/>
            <p className="p-2">اجرا شده توسط شرکت مهندسی هوشمند سازگان</p> 
        </div>
    </div>
  );
}

export default FormContainer;
  