import { Fragment } from "react";
import {Modal} from "react-bootstrap";

const ResponseModal = (props) => {
    const {finalResponse, onClosed, show} = props

    return (
        <Fragment>
            {
                finalResponse 
                && 
                <Modal    
                  dir="rtl"                
                  backdrop="static"
                  keyboard={false}
                  size="md"
                  aria-labelledby="contained-modal-title-vcenter"
                  centered
                  dialogClassName="pointer-events-none" 
                  show={show} 
                  onHide={onClosed}>
                    <Modal.Header className={finalResponse.error ? "bg-danger text-white": "bg-success text-white"}>
                        <Modal.Title id="contained-modal-title-vcenter text-center">
                            <Fragment>
                                {
                                    finalResponse.msg
                                    ?
                                    <h5>
                                      عملیات ناموفق
                                    </h5>
                                    :
                                    <h5>
                                      عملیات موفق
                                    </h5>
                                }
                            </Fragment>
                        </Modal.Title>
                        <button type="button" className="close" datadismiss="modal" onClick={onClosed}>
                        {/* <span>×</span> */}
                        {/* <i class="ico-times" role="img" aria-label="Cancel"></i> */}
                        {/* <span aria-hidden="true">&times;</span> */}
                        {/* <i class="fa fa-close"></i> */}
                        {/* <i style={{fontSize:"24px"}} class="fa">&#xf00d;</i> */}
                          {/* <i className=""></i> */}
                              {/* &times; */}
                              &#10006;
                        </button>
                    </Modal.Header>
                    <Modal.Body>
                        <div>
                            {
                                finalResponse.msg
                                ?
                                  <p dir="rtl" className="text-dark font-weight-bold">{finalResponse.msg._text !== "" ? finalResponse.msg._text : "عملیات ناموفق بود."}</p>
                                :
                                <ul>                                    
                                    <p className="text-dark" dir="rtl">شماره كارت : {finalResponse.card_no._text}</p>
                                    <p className="text-dark" dir="rtl">تاریخ تراکنش : {finalResponse.tr_date._text}</p>
                                    <p className="text-dark" dir="rtl">زمان تراکنش : {finalResponse.tr_time._text}</p>
                                    <p className="text-dark" dir="rtl">شماره تراکنش : {finalResponse.tr_no._text}</p>
                                    <p className="text-dark" dir="rtl">مبلغ به ریال :	{finalResponse.amount._text}</p>
                                    <p className="text-dark" dir="rtl">کد بانک : {finalResponse.bank_code._text}</p>
                                    <p className="text-dark" dir="rtl">شماره حساب : {finalResponse.account_no._text}</p>
                                </ul>
                            }
                        </div>
                    </Modal.Body>
                    {
                        finalResponse.account_no
                        &&
                        <Modal.Footer className="bg-light">
                            <div className="d-flex justify-content-center w-100">
                                <button onClick={onClosed} className="btn btn-dark btn-100"> آغاز عملیات جدید
                                    {/* <i className="fas fa-trash"></i> آغاز عملیات جدید */}
                                </button>
                            </div>
                        </Modal.Footer>
                    }
                </Modal>
            }
        </Fragment>    
    );
}

export default ResponseModal;
  