import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { endPointGetOperationTypesAndFields, endPointPos, endPointPcPosAgent } from '../store/api';
import ResponseModal from "./ResponseModal";
import { xml2json } from "xml-js";
import LoadingModal from './LoadingModal';

function MainForm() {

    const operationTypeRef = useRef(null);
    const fieldRef = useRef(null);
    const branchCodeRef = useRef(null);
    const agentCodeRef = useRef(null);
    const amountRef = useRef(null);

    const [operationTypes, setOperationTypes] = useState([]);
    const [fields, setFields] = useState([]);
    const [amountOfSelectedOperationType, setAmountOfSelectedOperationType] = useState("3000");
    const [selectedFieldCode, setSelectedFieldCode] = useState("50");
    const [inputErrors, setInputErrors] = useState("")
    const [inputsAreValid, setInputsAreValid] = useState(false)
    const [branchCodeValue, setBranchCodeValue] = useState('')
    const [agentCodeValue, setAgentCodeValue] = useState('')
    const [finalResponse, setFinalResponse] = useState(false)
    const [alertModalShow, setAlertModalShow] = useState(false)
    const [loadingModalShow, setLoadingModalShow] = useState(false)    

    const handleClose = () => setAlertModalShow(false);
    const handleShow = () => setAlertModalShow(true);

    const fetchData = () => {
        axios.get(endPointGetOperationTypesAndFields()) 
        .then(res=>{
            setLoadingModalShow(true)
            setOperationTypes(res.data.operationTypeSet);
            setFields(res.data.fieldSet);  
            setLoadingModalShow(false)   
        })
        .catch(err =>
            console.log("fetchError", err)
        ) 
    };
        
    useEffect(() => { 
        fetchData();        
    },[]);

    useEffect(() => {
        if(operationTypes.length !== 0){
            const selectedOperationTypes = operationTypes.find(obj => {
                return obj.typeName ==="تست کد نمایندگی";
            }); 
            if(selectedOperationTypes){ 
                setAmountOfSelectedOperationType(selectedOperationTypes.amount)
            }
        }
    },[operationTypes]);

    useEffect(() => {
        if(fields.length !==0){
            const selectedField = fields.find(obj => {
                return obj.fieldName ==="ثالث";
            });  
            if(selectedField){
                setSelectedFieldCode(selectedField.fieldCode)
            }
        }
    },[fields]);

    const handleValidation = () => {
        // console.log("btn Clicked2")
        let inputsWereValid = false;

        let operationTyp = operationTypeRef.current.value;
        let field = fieldRef.current.value;
        let branchCode = branchCodeRef.current.value;
        let agentCode = agentCodeRef.current.value;

        if( operationTyp !== "null" ){
            if( field !== "null" ){
                if( branchCode !== ''){
                    if( branchCode.length === 3 ){
                        if(agentCode !== ''){
                            if( (agentCode > 0) && (agentCode.length >= 3) && (agentCode.length <= 6) ){
                                inputsWereValid = true;      
                            }else{
                                setInputErrors("مقدار کد نمایندگی باید بزرگتر از صفر و بین سه تا شش رقم باشد.")
                            }
                        }else{
                            setInputErrors("وارد کردن کد نمایندگی ضروری است!")
                        }
                    }else{
                        setInputErrors("مقدار کد شعبه باید سه رقمی باشد!")
                    }
                }else{
                    setInputErrors("وارد کردن کد شعبه ضروری است!")
                }
            }else{
                setInputErrors("انتخاب رشته ضروری است!")
            }
        }else{
            setInputErrors("انتخاب نوع عملیات ضروری است!")
        }
        setInputsAreValid(inputsWereValid)
        return inputsWereValid;
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        // console.log("btn Clicked1")
        let inputsAreValid = await handleValidation();
        // console.log("btn Clicked3")
        if(inputsAreValid){
            let operation = operationTypeRef.current.value;
            let fieldCode = selectedFieldCode;
            let branchCode = branchCodeValue;
            let agentCode = agentCodeValue;
            let body = {
                "branchCode": branchCode,
                "agentCode": agentCode,
                "amount": amountOfSelectedOperationType,
                "fieldCode": fieldCode
            }
            // console.log("btn Clicked4")
            axios.post(endPointPos(), body)        
            .then(res =>{
                // console.log("btn Clicked5")
                setLoadingModalShow(true)
                // console.log("Response.length",res.data.shenaseh100.length);
                if(!(res.data.shenaseh100) || res.data.shenaseh100.length !== 100){
                    alert(res.data.error && res.data.error)                    
                }else{
                    let shenaseh100 = res.data.shenaseh100 && res.data.shenaseh100
                    var poststr;
                    if (operation === "balance") {
                        poststr = "operation="+encodeURI(operation);
                    } else {
                        poststr = "operation="+encodeURI(operation)+"&amount="+encodeURI(amountOfSelectedOperationType)+"&shenaseh="+encodeURI(shenaseh100);
                    }    
                    axios.post( 
                        endPointPcPosAgent(), 
                        poststr, 
                        {
                            "Content-Type": "application/xml; charset=utf-8"                
                        }
                    )
                    .then(resp=>{
                        var xml = resp.data;
                        var result1 = xml2json(xml, { compact: true, spaces: 4 });
                        const items = JSON.parse(result1);
                        // console.log("result1", result1)
                        // console.log("items", result1)
                        setFinalResponse(items.info)
                        setLoadingModalShow(false)
                        handleShow()
                    })
                    .catch(err=>
                        console.log("error", err)
                    )
                }
            })
            .catch(err =>
                console.log("error", err)
            )
            
        }               
        
    }

    const handleProcessTypeChange = () => {
        const selectedOperationTypes = operationTypes.find(obj => {
            return obj.typeName === operationTypeRef.current.value;
        });
        setAmountOfSelectedOperationType(selectedOperationTypes.amount)
    };

    const handleFieldChange = () => {
        console.log("fieldRef", fieldRef)
        const selectedField = fields.find(obj => {
            return obj.fieldName ==="سما";
        });  
        setSelectedFieldCode(selectedField.fieldCode)
    }
   
    const handleBranchCodeChange = (e) => {
        const result = e.target.value.replace(/\D/g, '');

        setBranchCodeValue(result);
    };

    const handleAgentCodeChange = (e) => {
        const result = e.target.value.replace(/\D/g, '');

        setAgentCodeValue(result);
    };

    return (
        <div className="mainForm">
            <form onSubmit={handleSubmit} className="pt-5">
                <div className="form-group row my-3">
                    <label htmlFor="processType" className="col-sm-5 col-form-label"> نوع عملیات *:</label>
                    <div className="col-sm-6">
                        <select 
                            // value= "تست کد نمایندگی"
                            ref={operationTypeRef} 
                            id="processType" 
                            className="form-control shadow" 
                            onChange={handleProcessTypeChange}
                        >
                            {
                                (operationTypes && operationTypes.length !== 0)
                                &&
                                operationTypes.map((eachType)=>
                                    <option key={eachType.id} value={eachType.typeName} selected={eachType.typeName === "تست کد نمایندگی" }>{eachType.typeName}</option>
                                )
                            }
                        </select>
                    </div>
                </div>
                <div className="form-group row my-3">
                    <label htmlFor="feild" className="col-sm-5 col-form-label"> رشته *:</label>
                    <div className="col-sm-6">
                        <select 
                            // value={"ثالث"}
                            ref={fieldRef} 
                            id="feild" 
                            className="form-control shadow" 
                            onChange={handleFieldChange}                        
                        >
                            {
                                (fields && fields.length !== 0)
                                &&
                                fields.map((eachField)=>
                                    <option key={eachField.id} value={eachField.fieldName} selected={eachField.fieldName === "ثالث" }>{eachField.fieldName}</option>
                                )
                            }
                        </select>
                    </div>
                </div>
                <div className="form-group row my-3">
                    <label htmlFor="branchCode" className="col-sm-5 col-form-label"> کد شعبه *:</label>
                    <div className="col-sm-6">
                        <input
                            ref={branchCodeRef}
                            type="text"
                            size="3"
                            className="form-control shadow"
                            id="branchCode"
                            error={"!!errors.firstname"}
                            title="مقدار کد شعبه باید سه رقمی باشد."
                            value={branchCodeValue}
                            onChange={handleBranchCodeChange}
                        />
                    </div>
                </div>
                <div className="form-group row my-3">
                    <label htmlFor="agentCode" className="col-sm-5 col-form-label"> کد نمایندگی *:</label>
                    <div className="col-sm-6">
                        <input
                            ref={agentCodeRef}

                            type="text"
                            className="form-control shadow"
                            id="agentCode"
                            value={agentCodeValue}
                            onChange={handleAgentCodeChange}
                            title="مقدار کد نمایندگی باید بین سه تا شش رقم باشد."
                        />
                    </div>
                </div>
                <div className="form-group row my-3">
                    <label htmlFor="testedPrice" className="col-sm-5 col-form-label">مبلغ به ریال:</label>
                    <div className="col-sm-6">
                        <input 
                            ref={amountRef} 
                            type="text" 
                            className="form-control shadow" 
                            id="testedPrice"
                            placeholder={amountOfSelectedOperationType}
                            disabled
                        />
                    </div>
                </div>               
                <button type="submit" className="btn btn-outline-info my-4 shadow" >ارسال به پایانه فروش</button>                
            </form>
            <p className='submitError'>
                {
                    !inputsAreValid
                    &&
                    inputErrors
                }
            </p>
            {
                finalResponse
                &&                
                <ResponseModal
                    show={alertModalShow}
                    onClosed={handleClose}
                    finalResponse = {finalResponse} 
                />
            } 
            {
                loadingModalShow
                &&
                <LoadingModal 
                    show={loadingModalShow}                
                />
            }
        </div>
    );
}

export default MainForm;
